.kitchen-21 {
  min-height: 800px;
  background-color: white;
}
.kitchen-header {
  display: inline-flex;
  border-top: 2px dotted black;
  border-bottom: 2px dotted black;
  padding: 0px !important;
}
.star {
  vertical-align: sub;
}
.discount-card {
  width: auto !important;
  height: 130px;
  margin-top: 2%;
}
.discount-body {
  text-align: center !important;
  border: 1px solid darkgray;
  border-radius: 15px;
  padding: 5px;
}
.react-multi-carousel-item {
  width: 345px !important;
  padding: 5px;
}
.react-multi-carousel-track {
  transition: transform 400ms ease-in-out 0s;
  overflow: unset;
}
.kitchen-hr {
  width: auto !important;
  margin-top: 0% !important;
}
.offer {
  vertical-align: sub;
  height: 20px !important;
  margin-right: -2%;
}
.acc-card {
  width: inherit !important;
  border: 1px solid rgba(0, 0, 0, 0.125) !important;
}
.toggle-btn {
  position: inherit !important;
  float: right;
}
.card-text,
.acc-header {
  color: orange;
  font-weight: bold;
}
.arrow-toggle {
  transform: rotate(-180deg);
  transition: transform 150ms ease;
}
.item-name {
  padding: 6% !important;
  display: inline-flex;
}
.item-title {
  padding: 0px !important;
}
.item-img {
  padding: 0px !important;
  display: inline-flex;
  align-items: flex-end;
}
.rupee-style {
  height: 23px !important;
  width: 22px !important;
}
.kitchen-img {
  width: 200px;
  height: 180px;
  float: right;
  border-radius: 5px;
}
@media (max-width: 376px) {
  .kitchen-img {
    width: 150px;
    height: 120px;
  }
  .add-btn {
    width: 80% !important;
  }
}
@media (min-width: 377px) and (max-width: 425px) {
  .kitchen-img {
    width: 150px;
    height: 120px;
  }
  .add-btn {
    width: 70% !important;
  }
}
.add-btn {
  color: #212529 !important;
  background-color: #ffc107db !important;
  border-color: #ffc107 !important;
  padding: 15px !important;
  height: 16% !important;
  width: 30%;
  margin-bottom: 2%;
  margin-left: 11%;
}
