.home {
  height: max-content;
  background: #f6f6fa;
  padding: 0px !important;
  border: 1px slid #808080;
  text-align: center;
  padding: 1rem 1rem 1rem 1rem !important;
}
.food-type {
  height: max-content;
  background: white;
  border: 1px slid #808080;
  margin-bottom: 3%;
}
.food-header {
  display: inline-flex;
}
.see-all {
  text-align: end;
}
.category {
  text-align: initial;
}
.category-hr {
  width: 96% !important;
  margin-top: 0px !important;
  margin-right: 0px !important;
  background: #e1e1e6 !important;
  margin: 2% !important;
}
.card {
  border: none !important;
  padding: 10px;
}
@media (max-width: 450px) {
  .card-mob {
    width: 50% !important;
  }
  .product {
    font-size: 1.25rem !important;
    font-family: cursive;
    font-weight: bolder;
  }
  .feature-header h4 {
    font-size: 1.25rem !important;
    font-weight: bold;
  }
  .feature-img {
    width: 80% !important;
  }
  .feature-body {
    margin-left: 10% !important;
  }
}
.card-main {
  display: inline-flex !important;
}
.product {
  font-size: 2.25rem !important;
  font-family: cursive;
  font-weight: bolder;
}
.feature-header h4 {
  font-size: 2.25rem;
  font-weight: bold;
}
.card-title {
  text-align: center;
}
.feature-img {
  width: 18rem;
  height: 20rem !important;
}
.caraousal-header {
  text-align: center;
  font-weight: bold;
  font-family: cursive;
}
.caraousal-header h4 {
  font-size: 2.5rem !important;
  font-weight: bold;
}
