.brand-header {
  text-align: center;
  height: max-content;
  border-radius: 5px;
  padding: 2%;
}
.category {
  display: flex;
}
.caraousal-header {
  text-align: center;
  font-weight: bold;
  font-family: cursive;
}
.caraousal-header h4 {
  font-size: 2.5rem !important;
  font-weight: bold;
}
.react-multi-carousel-list {
  padding: 5%;
}
.card-img-top {
  width: 18rem;
  height: 12rem;
}
.card-body {
  padding: 0px !important;
}
.react-multiple-carousel__arrow--left,
.react-multiple-carousel__arrow--right {
  background-color: rgb(0 0 0 / 55%) !important;
  height: 0px !important;
  margin-top: 3%;
}
.react-multiple-carousel__arrow--left {
  left: calc(-1% + 1px);
}
.react-multiple-carousel__arrow--right {
  right: calc(1% + 1px);
}
.card {
  width: min-content;
}
.first-header {
  display: inline-flex;
  text-align: center;
}
.scooter-img img {
  width: 70px;
}
hr {
  height: 0px;
  width: 53px;
  margin-top: 22%;
  margin-right: 5px;
  margin-left: 5px;
  background: antiquewhite;
  margin-bottom: 0px;
}
.second-header {
  color: white;
  text-align: center;
  font-family: cursive;
  margin-bottom: 2%;
}
h1 {
  line-height: 0.8 !important;
}
.container {
  background: transparent;
  font-size: 100%;
  font-weight: normal;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: top;
}
.centered {
  position: absolute;
  top: 89%;
  left: 50%;
  transform: translate(-50%, -80%);
  color: black;
}
@media (max-width: 550px) {
  h1 {
    font-size: 14px !important;
  }
  h2 {
    font-size: 12px !important;
  }
  .card {
    width: 100% !important;
    height: 100%;
    padding: 5px;
  }
  .card-img-top {
    width: 100%;
    height: 100%;
  }
  a {
    height: 100%;
  }
  .react-multi-carousel-list {
    padding: 0px !important;
  }
  .caraousal-header h4 {
    font-size: 1.25rem !important;
    font-weight: bold;
  }
}
