header {
  width: 100%;
  background: #f46732;
  height: 55px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 12;
  /* border-bottom: 2px solid white;
  border-radius: 10px;
  border-right: 1px solid white;
  border-left: 1px solid white; */
  margin-bottom: 2%;
}
Form {
  float: right;
}
.location {
  display: flex;
}
Link:active {
  color: #fbcf6b !important;
  background: black;
}
.active {
  color: #fbcf6b !important;
  background: black;
}
Nav {
  background: rgba(0, 0, 0, 0.3);
  height: 55px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 12;
}
.btn {
  display: inherit !important;
}
button {
  background-color: white !important;
  margin-left: 2%;
  position: absolute;
  outline: none !important;
  color: black !important;
  height: 58%;
  border-color: white !important;
  padding: 0px !important;
}
.btn-primary.focus,
.btn-primary:focus {
  box-shadow: none !important;
}
.btn.focus,
.btn:focus {
  box-shadow: none !important;
}
.btn:focus svg {
  color: yellowgreen;
}
.nav {
  /* line-height: 2rem !important; */
  margin-bottom: 3%;
}
.navbar {
  padding: 0px !important;
}
.container,
#responsive-navbar-nav,
.navbar-nav {
  height: 3.5em !important;
}
svg {
  width: 40px;
  height: 23px !important;
  top: 20%;
  margin-right: 1%;
  background: none;
  cursor: pointer;
}

.nav-header {
  margin-top: 0.45em;
  display: inline-flex;
}
.greet-header-text {
  padding: 0px !important;
  color: white;
  text-align: center;
  top: 12%;
}
.form-control {
  padding-top: 0.375rem;
  padding-right: 0.75rem;
  padding-bottom: 0.375rem;
  padding-left: 3.75rem;
  border-radius: 5px;
}
.search-btn {
  float: right;
  margin-bottom: 2%;
}
@media (max-width: 450px) {
  .btn-outline-success {
    height: 80% !important;
  }
  .nav-header {
    display: block !important;
  }
  header {
    height: 40% !important;
  }
  .greet-header-text {
    top: 5% !important;
    text-align: left !important;
    margin-bottom: 5% !important;
    padding-left: 5%;
  }
  .h5,
  h5 {
    font-size: 1rem;
  }
  button {
    margin-top: 1px !important;
    height: 90%;
  }
}
@media (min-width: 1024px) {
  header {
    height: 30% !important;
  }
}
